import * as actionType from './actionType'
import {getData} from 'util'
import {API_RETURN_REASON_LIST_PAGE, API_RETURN_REASON_CREATE, API_RETURN_REASON_DELETE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.RETURNREASONSETTING_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.RETURNREASONSETTING_UPDATE, data: {tableLoading: true}})
  getData({api: API_RETURN_REASON_LIST_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.RETURNREASONSETTING_UPDATE,
      data: {
        tableLoading: false,
        searchResult: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.RETURNREASONSETTING_FILE
    })
  })
}

// 创建/编辑货主
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.RETURNREASONSETTING_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_RETURN_REASON_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.RETURNREASONSETTING_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.RETURNREASONSETTING_FILE
    })
  })
}

// 删除货主
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_RETURN_REASON_DELETE, ...reqData}).then(data => {
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.RETURNREASONSETTING_FILE
    })
  })
}


