import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  message
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.returnReasonSetting}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 新增
  handleAdd = () => {
    const {
      searchParams,
      handleAddOrEdit
    } = this.props
    if (!searchParams.reasonCode) {
      message.error('请输入类型编号')
      return
    }
    if (!searchParams.reasonRemark) {
      message.error('请输入退货类型')
      return
    }
    handleAddOrEdit({data: {...searchParams}}, () => {
      message.success('新增成功')
      const {handleGetList, update} = this.props
      update({searchParams: {}})
      handleGetList()
    })
  }

  render() {
    const {
      searchParams: {
        reasonCode,
        reasonRemark,
      }
    } = this.props

    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>类型编号：</label>
              <div className='box'>
                <Input value={reasonCode} placeholder='请输入' onChange={(e) => {
                  this.onInputChange({reasonCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>退货类型：</label>
              <div className='box'>
                <Input value={reasonRemark} placeholder='请输入' onChange={(e) => {
                  this.onInputChange({reasonRemark: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col>
              <Button type="primary" style={{marginLeft: '16px'}} onClick={this.handleAdd}>新增</Button>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



